/**
 * @component Modal
 * @description
 *
 * ##  / Modal
 *
 * ## 
 *
 * Model, , , H5 historypopstate, Modal, modal.
 *
 * ## 
 *
 * - modalmodal
 * - modalurl
 * - modal, `component`, `data`,
 *    - this.$options.$data.username 
 *    - this.username , 
 * - `onDismiss`modal.
 * - Page, 
 *
 * ### 
 *
 * mode, , mode. , ```animateName```:
 *
 * - : 
 * - zoom: 
 * - fade: 
 * - fade-right: 
 *
 * @usage
 * // 
 * openModal () {
 *        this.$modal.present({
 *          animateName: 'zoom',
 *          component: modalPageComponent,
 *          data: {hello: 'Page1Data'},
 *          showBackdrop: true,
 *          enableBackdropDismiss: true,
 *          onDismiss (data) {
 *            console.debug('modal1')
 *            console.debug(JSON.stringify(data))
 *          }
 *    })
 * },
 *
 * // 
 * closeModal () {
 *        this.$modal.dismiss({
 *          result: 'modal 1 dismissed success!'
 *        })
 * }
 * @demo #/modal
 *
 * */

import Vue from 'vue'
import registerListener from '../../util/register-listener'
import modalComponent from './modal.vue'
import getInsertPosition from '../../util/get-insert-position'

let modalArr = []       // modal
let unRegisterUrlChange = []
let isModalEnable = true // modalfalse, true
let scrollTop = 0

/**
 * 
 * @private
 */
function ModalFactory (options) {
  const Modal = Vue.extend(modalComponent)
  options.dismissOnPageChange = false
  options.recordInHistory = false
  options.scrollControl = false
  return new Modal({
    el: getInsertPosition('modalPortal').appendChild(
      document.createElement('div')
    ),
    propsData: options
  })
}

/**
 * Modal
 * : ()/(modal)/(template), Modal, templateModal, urlChange. Modal,
 * modal_present.
 *
 * @param {Object} options
 * @param {String} [options.animateName] - modal, : /zoom/fade/fade-enter
 * @param {object} options.component - modal, 
 * @param {object} [options.data] - modal, componentdata, , .
 * @param {function} [options.onDismiss] - model, data
 * @param {Boolean} [options.showBackdrop=true] - backdrop
 * @param {Boolean} [options.enableBackdropDismiss=true] - backdrop
 * @example
 * :
 * {
 *  component:require('*.vue'),  // modal
 *  data:{...},            // modal
 *  onDismiss(data){....},      // model, data
 * }
 *
 * 
 * - this.$options.$data.username 
 * - this.username , 
 *
 * */
function present (options = {}) {
  isModalEnable = false
  return new Promise((resolve) => {
    let modalInstance = ModalFactory(options)
    let presentPromise = modalInstance.present()
    // 
    window.history.pushState(
      {
        id: new Date().getTime(),
        name: `Modal-${modalInstance._uid}`
      },
      '',
      ''
    )

    // record
    modalArr.push(modalInstance)

    // 
    if (unRegisterUrlChange.length === 0) {
      // url
      registerListener(
        window,
        'popstate',
        function () {
          //   window.history.back() 
          isModalEnable && dismiss(null, false)
        },
        {},
        unRegisterUrlChange
      )
      //  Doc 
      _disableScroll()
    }

    presentPromise.then(() => {
      isModalEnable = true
      resolve()
    })
  })
}

/**
 * dismiss
 * dismissModal, onDismiss, , , modal, modal, .
 * `dataBack``onDismiss`
 * @param {*} [dataBack] -  modaldismiss
 * @param {Boolean} [changeLocalHistory=true] -  , true
 * */
function dismiss (dataBack, changeLocalHistory = true) {
  isModalEnable = false

  if (changeLocalHistory) {
    window.history.back()
  }

  return new Promise(resolve => {
    // modal
    let lastModalInstance = modalArr.pop()
    // 
    if (modalArr.length === 0) {
      // urlChange
      unRegisterAllListener()
      // 
      _enableScroll()
    }

    lastModalInstance.dismiss(dataBack).then(() => {
      isModalEnable = true
      // onDismiss
      resolve()
    })
  })
}

// 
function unRegisterAllListener () {
  unRegisterUrlChange.forEach(item => item && item())
  unRegisterUrlChange = []
}

function _disableScroll () {
  let scrollHeight = window.document.documentElement.scrollHeight
  let clientHeight = window.document.documentElement.clientHeight
  if (scrollHeight > clientHeight) {
    scrollTop = window.scrollY
    window.document.body.style.position = 'fixed'
    window.document.body.style.top = -scrollTop + 'px'
  }
}

function _enableScroll () {
  scrollTop = window.scrollY
  window.document.body.style.position = ''
  window.document.body.style.top = ''
  window.scrollTo(0, scrollTop)
  scrollTop = 0
}

export default {
  present,
  dismiss
}



