<template>
    <div class="ion-img img" :class="{'img-loaded':isLoaded,'img-unloaded':!isLoaded}" :src="src"
         :style="{'width':w,'height':h}">
        <transition name="fade">
            <img v-show="isLoaded" ref="img" :alt="alt" :src="srcValue" :width="w" :height="h">
        </transition>
    </div>
</template>
<script type="text/javascript">
  import { isPresent } from '../../util/type'
  import registerListener from '../../util/register-listener'

  export default {
    name: 'Img',
    inject: {
      contentComponent: {
        from: 'contentComponent',
        default: null
      }
    },
    props: {
      src: String,
      alt: {
        type: String,
        default: 'image'
      },
      width: {
        type: [Number, String],
        default: 0
      },
      height: {
        type: [Number, String],
        default: 0
      }
    },
    data () {
      return {
        isLoaded: false,        // DOMimg
        srcValue: null,         // src
        w: this.wQ,             // 
        h: this.hQ,             // 

        canRequest: false,      // content!
        canRender: false,       // content!

        hasLoaded: false,       // 
        requestingSrc: null,    // src
        renderedSrc: null,      // src
        rect: null,             // 
        imgElement: null,       // img
        wQ: this.getUnitValue(this.width) || 0,  // 
        hQ: this.getUnitValue(this.height) || 0, // 

        unRegLoadImg: null      // {function} 
      }
    },
    watch: {
      width () {
        this.setDims()
      },
      height () {
        this.setDims()
      },
      src () {
        this.initSrcValue()
      }
    },
    methods: {
      /**
       * 
       * @private
       * */
      init () {
        // console.assert(this.contentComponent, 'ImgContent!')

        // img
        this.imgElement = this.$refs.img

        // 
        this.setDims()

        // src
        this.initSrcValue()

        this.contentComponent && this.contentComponent.$_addImg(this)
      },

      /**
       * src,imgsrc, 
       * srcthis.src, , this.src=>this.requestingSrc, .
       * @private
       * */
      reset () {
        if (this.requestingSrc && !this.renderedSrc && !this.hasLoaded) {
          // , , 
          // console.warn(`abortRequest ${this.requestingSrc} ${Date.now()}`);
          this.srcAttr('')
          this.requestingSrc = null
        }
        if (this.renderedSrc) {
          // , , 
          // console.warn(`hideImg ${this.renderedSrc} ${Date.now()}`);
          this.setLoaded(false)
        }
      },

      /**
       * 
       * @private
       * */
      update () {
        // Content => ImgcanRequestcanRender
        if (this.src && this.contentComponent && this.contentComponent.$_isImgUpdatable()) {
          if (this.canRequest && (this.src !== this.renderedSrc && this.src !== this.requestingSrc) && !this.hasLoaded) {
            // 
            // imgload, 
            if (!this.unRegLoadImg) {
              this.unRegLoadImg = registerListener(this.imgElement, 'load', () => {
                this.unRegLoadImg && this.unRegLoadImg()
                this.unRegLoadImg = null
                this.hasLoaded = true // img loaded success!
                this.update()
              }, {passive: true})
            }

            // ,
            this.requestingSrc = this.src
            this.setLoaded(false)
            this.srcAttr(this.src)
            // 
            this.setDims()
          }

          if (this.canRender && this.hasLoaded) {
            if (this.src !== this.renderedSrc) {
              // , 
              this.renderedSrc = this.src
              // 
              this.setDims()
              this.srcAttr(this.src)
              this.setLoaded(true)
            } else {
              // , 
              this.setLoaded(true)
            }
          }
        }
      },

      /**
       * DOM
       * @private
       * */
      setLoaded (isLoaded) {
        this.isLoaded = isLoaded
      },

      /**
       * imgbox
       * @private
       * */
      setDims () {
        // 
        this.wQ = this.getUnitValue(this.width) || 0
        this.hQ = this.getUnitValue(this.height) || 0

        if (this.w !== this.wQ || this.h !== this.hQ) {
          if (this.w !== this.wQ) {
            this.w = this.wQ
          }
          if (this.h !== this.hQ) {
            this.h = this.hQ
          }
        }
      },

      /**
       * /propsrc
       * @private
       * */
      initSrcValue () {
        // src,imgsrc, 
        this.reset()
        if (this.src.indexOf('data:') === 0) {
          // datauri, 
          this.hasLoaded = true
        } else {
          // src, 
          this.hasLoaded = false
        }
        this.update()
      },

      /**
       * imgsrc, img
       * @param {string} srcAttr - imgsrc
       * @private
       * */
      srcAttr (srcAttr) {
        this.srcValue = srcAttr
      },

      /**
       * heightwidth
       * @param {any} val
       * @return {string}
       * @private
       */
      getUnitValue (val) {
        if (isPresent(val)) {
          if (typeof val === 'string') {
            if (val.indexOf('%') > -1 || val.indexOf('px') > -1) {
              return val
            }
            if (val.length) {
              return val + 'px'
            }
          } else if (typeof val === 'number') {
            return val + 'px'
          }
        }
        return ''
      },

      /**
       * 
       * @private
       * */
      getBounds () {
        if (!this.rect) {
          // DOM
          this.rect = this.$el.getBoundingClientRect()
        }
        return this.rect
      },

      /**
       * 
       * @private
       * */
      getBottom () {
        const bounds = this.getBounds()
        return (bounds && bounds.bottom) || 0
      },

      /**
       * 
       * @private
       * */
      getTop () {
        const bounds = this.getBounds()
        return (bounds && bounds.top) || 0
      }
    },
    mounted () {
      this.init()
    },
    destroyed () {
      this.unRegLoadImg && this.unRegLoadImg()
      this.unRegLoadImg = null
      this.reset()
    }
  }
</script>



