/**
 * @component ToastState
 * @description
 *
 * ##  / ToastState
 *
 * Toast, ToastState, Toast, :
 *
 * - success
 * - fail
 * - offline
 * - loading
 *
 * , success, fail, offlineIcon, loadingSpinner, propsSpinner.
 *
 *```
 ToastState.present('Bottom was added successfully')
 *```
 *
 * #### 2. MessageDuration
 *
 *```
 ToastState.present('Bottom was added successfully',1000)
 *```
 *
 * #### 3. Options
 *
 *```
 ToastState.present({
    message: '',
    duration: 3000,
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('2 onDismiss in promise success!')
    }
 })
 ToastState.present({
    message: '',
    duration: 3000,
    type: 'loading',
    spinner: {
      name:'ios'
    },
    cssClass: 'showCloseBtnToastCssClass',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('5 onDismiss in promise success!')
    }
 })
 ToastState.present({
    message: '',
    duration: 3000,
    type: 'success',
    cssClass: 'showCloseBtnToastCssClass',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('5 onDismiss in promise success!')
    }
 })
 *```
 *
 * #### 4. present
 *
 *```
 ToastState.present('Bottom was added successfully',1000)
 *```
 *
 * @props {String} message - Toastmessage, 
 * @props {Number} [duration=3000] - Toast, 
 * @props {String} [cssClass] - , 
 * @props {Boolean} [dismissOnPageChange=false] - , , 
 * @props {String} [onDismiss=noop] - 
 * @props {Object} [spinner] - Spinnerprops
 * @props {String} [type=''] - toast  success, fail, offline, loading
 *
 * @demo #/toast-state
 */
import ToastComponent from './toast-state.vue'
import GeneratePopUpInstance from '../../util/GeneratePopUpInstance.js'
import { isObject, isString } from '../../util/type'

class ToastInstance extends GeneratePopUpInstance {
  normalizeOptions () {
    let _args = Array.prototype.slice.call(arguments)
    let propsData = {}

    // get el position
    if (_args.length === 2) {
      propsData = {
        message: _args[0],
        duration: _args[1]
      }
    }

    if (_args.length === 1 && isString(_args[0])) {
      propsData = {
        message: _args[0]
      }
    }

    if (_args.length === 1 && isObject(_args[0])) {
      propsData = _args[0]
    }

    // toastscroll
    propsData.scrollControl = false
    return propsData
  }

  isPresentHandled (options) {
    return !options.isH5 &&
      window.VM &&
      window.VM.platform &&
      window.VM.platform.showToast &&
      window.VM.platform.showToast(options)
  }

  isDismissHandled () {
    return window.VM &&
      window.VM.platform &&
      window.VM.platform.hideToast &&
      window.VM.platform.hideToast()
  }
}

function _toast () {
  let _instance = new ToastInstance(ToastComponent, 'toastPortal')
  // 
  let _args = Array.prototype.slice.call(arguments)
  _instance.present(..._args)
  return _instance
}

_toast.present = _toast

export default _toast



