import Vue from 'vue'
import getInsertPosition from './get-insert-position'
import {isObject} from './type'

/**
 * PopUp
 * @private
 * */
export default class GeneratePopUpInstance {
  /**
   * @param {Object} component - 
   * @param {String} [position=null] - DOM
   * */
  constructor (component, position = null) {
    this._component = component        // 
    this._position = position  // popup(id)
    this._ins = null                   // PopUp
  }

  /**
   *  promise , alipay
   * @param {Object} options - 
   * @override
   * */
  isPresentHandled (options) {
    return false
  }

  /**
   *  dismiss , alipay
   * @param {Object} options - 
   * @override
   * */
  isDismissHandled (options) {
    return false
  }

  /**
   * present, propsData,
   * options
   * @param {Object} options - 
   * @override
   * */
  normalizeOptions (options) {
    let _args = Array.prototype.slice.call(arguments)
    if (isObject(_args[0])) {
      return _args[0]
    } else {
      return {}
    }
  }

  /**
   * 
   * @return {Object}
   * @override
   * */
  getInstance () {
    return this._ins
  }

  /**
   * 
   * @param {Object} options - 
   * @private
   * */
  generateInstance (options) {
    let el = getInsertPosition(this._position).appendChild(
      document.createElement('div')
    )
    let Component = Vue.extend(this._component)
    let _ins = new Component({propsData: options})
    _ins.$mount(el)
    return _ins
  }

  /**
   * 
   * @return {Promise}
   * @public
   * */
  present () {
    let options = this.normalizeOptions(...arguments)
    return new Promise((resolve) => {
      if (!this.isPresentHandled(options)) {
        if (this._ins && this._ins.isActive) {
          this._ins.dismiss().then(() => {
            this._ins = this.generateInstance(options)
            this._ins.present().then(() => resolve())
          })
        } else {
          // normal present
          this._ins = this.generateInstance(options)
          this._ins.present().then(() => resolve())
        }
      } else {
        resolve()
      }
    })
  }

  /**
   * 
   * @return {Promise}
   * @public
   * */
  dismiss () {
    return new Promise((resolve) => {
      this.isDismissHandled()

      /* istanbul ignore else */
      if (this._ins && this._ins.isActive) {
        this._ins.dismiss().then(() => {
          this._ins = null
          resolve()
        })
      } else {
        this._ins = null
        resolve()
      }
    })
  }
}



