/**
 * @component Toast
 * @description
 *
 * ##  / Toast
 *
 * Toast, . Toast//, , Toast, .
 *
 * ### 
 *
 * Toast, `Toast('Toast Bottom Only String')`, :
 * toast? , Toast:
 *
 * 1. duration 
 * 2. showCloseButton 
 * 3. , 
 * 4. `let toast = Toast('Toast Bottom Only String')`toast, `toast.dismiss()`
 *
 * Toast.
 *
 *
 * ### 
 *
 * - Toast`onDismiss`, Toast.
 * - `dismissOnPageChange`Toast, `false`.
 *
 * ### Toast
 *
 * #### 1. Message
 *
 *
 *```
 Toast('Bottom was added successfully')
 *```
 *
 * #### 2. MessageDuration
 *
 *```
 Toast('Bottom was added successfully',1000)
 *```
 *
 * #### 3. Options
 *
 *```
 Toast({
    message: 'Bottom was added successfully',
    duration: 3000,
    position: 'bottom',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('2 onDismiss in promise success!')
    }
 })
 Toast({
    message: 'Top with Button was added successfully',
    duration: 3000, // 
    position: 'top',
    showCloseButton: true,
    closeButtonText: '',
    cssClass: 'showCloseBtnToastCssClass',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('5 onDismiss in promise success!')
    }
 })
 *```
 *
 * #### 4. present
 *
 *```
 Toast.present('Bottom was added successfully',1000)
 *```
 *
 * @props {string} message - Toastmessage, 
 * @props {number} [duration=3000] - Toast, 
 * @props {string} [position="bottom"] - Toast. : "top", "middle", "bottom".
 * @props {string} [cssClass] - , 
 * @props {boolean} [showCloseButton=false] - 
 * @props {string} [closeButtonText='Close'] - , Close
 * @props {boolean} [dismissOnPageChange=false] - , , 
 * @props {string} [mode='ios'] - 
 * @props {string} [onDismiss=noop] - 
 * @props {string} [type=''] - (/DingTalk) toast  success / fail
 *
 * @demo #/toast
 */

import ToastComponent from './toast.vue'
import GeneratePopUpInstance from '../../util/GeneratePopUpInstance.js'
import { isObject, isString } from '../../util/type'

class ToastInstance extends GeneratePopUpInstance {
  normalizeOptions () {
    let _args = Array.prototype.slice.call(arguments)
    let propsData = {}

    // get el position
    if (_args.length === 2) {
      propsData = {
        message: _args[0],
        duration: _args[1]
      }
    }

    if (_args.length === 1 && isString(_args[0])) {
      propsData = {
        message: _args[0]
      }
    }

    if (_args.length === 1 && isObject(_args[0])) {
      propsData = _args[0]
    }

    // toastscroll
    propsData.scrollControl = false
    return propsData
  }

  isPresentHandled (options) {
    return !options.isH5 &&
      window.VM &&
      window.VM.platform &&
      window.VM.platform.showToast &&
      window.VM.platform.showToast(options)
  }

  isDismissHandled () {
    return window.VM &&
      window.VM.platform &&
      window.VM.platform.hideToast &&
      window.VM.platform.hideToast()
  }
}

/**
 * Toast
 * @function present
 * @description Toast
 * @param {object} args - 
 * @param {string} args.message - The message for the toast. Long strings will wrap and the toast container will
 *   expand.
 * @param {number} [args.duration=3000] - How many milliseconds to wait before hiding the toast.
 * @param {string} [args.position="bottom"] - The position of the toast on the screen. Accepted values: "top",
 *   "middle", "bottom".
 * @param {string} [args.cssClass] - Additional classes for custom styles, separated by spaces.
 * @param {boolean} [args.showCloseButton=false] - Whether or not to show a button to close the toast.
 * @param {string} [args.closeButtonText='Close'] - Text to display in the close button.
 * @param {boolean} [args.dismissOnPageChange=false] - Whether to dismiss the toast when navigating to a new page or
 *   nav back.
 * @return {ToastInstance} Toast
 * @private
 * */
function _toast () {
  let _instance = new ToastInstance(ToastComponent, 'toastPortal')
  // 
  let _args = Array.prototype.slice.call(arguments)
  _instance.present(..._args)
  return _instance
}

_toast.present = _toast

export default _toast



