<template>
    <transition
            name="fade"
            @before-enter="beforeEnter"
            @after-leave="afterLeave">
        <div class="ion-backdrop"
             @click="bdClick"
             @touchmove="onTouchMoveHandler($event)"
             :class="{'backdrop-no-tappable':!enableBackdropDismiss,'fixed':fixed}"
             :style="{'left':left+'px','top':top+'px'}"
             v-show="isActiveLocal"></div>
    </transition>
</template>
<script type="text/javascript">
  export default {
    name: 'Backdrop',
    data () {
      return {
        isActiveLocal: this.isActive,   // present/dismiss
        count: 0                        // 
      }
    },
    props: {
      enableBackdropDismiss: {
        type: Boolean,
        default: true
      },
      // backdrop
      isActive: Boolean,
      // 
      bdClick: {
        type: Function,
        default: function () {}
      },
      /**
       * backdrop, 
       * */
      top: {
        type: Number,
        default: 0
      },
      left: {
        type: Number,
        default: 0
      },
      // positionfixed, 
      fixed: Boolean
    },
    watch: {
      isActive () {
        this.isActiveLocal = this.isActive
      }
    },
    methods: {
      /**
       * 
       * @private
       */
      beforeEnter () {
        /**
         * @event component:Backdrop#onShown
         * @description Backdrop
         */
        this.$emit('onShown')
      },
      afterLeave () {
        /**
         * @event component:Backdrop#onHidden
         * @description Backdrop
         */
        this.$emit('onHidden')
      },
      onTouchMoveHandler ($event) {
        $event.preventDefault()
        $event.stopPropagation()
      }
    }
  }
</script>



