/**
 * @function present
 * @description
 * ,  `Promise`  `resolve` 
 *
 * @param {object} options - 
 * @param {String} [options.title]                        - 
 * @param {string} [options.subTitle]                     - 
 * @param {string} [options.message]                      - 
 * @param {string} [options.cssClass]                     - 
 * @param {Boolean} [options.enableBackdropDismiss=true]  - backdrop
 * @param {Boolean} [options.dismissOnPageChange=true]    - 
 * @param {string} [options.mode=ios]                     - 
 * @param {string} [options.isH5=false]                   - H5(HybridAlert)
 *
 * @param {Array} [options.buttons]                       - buttonrole
 * @param {Array} options.buttons.text                    - button
 * @param {Array} options.buttons.icon                    - buttoniconname, Icon
 * @param {Array} options.buttons.role                    - cancel()/destructive(/)/null
 * @param {Array} options.buttons.handler                 - 
 * @param {Array} options.buttons.cssClass                - 
 *
 * @param {Array} [options.inputs]                        - alertinputform
 * @param {Array} options.inputs.type                     - input, : text, tel, number, etc.
 * @param {Array} options.inputs.name                     - 
 * @param {Array} options.inputs.placeholder              - 
 * @param {Array} options.inputs.value                    - 
 * @param {Array} options.inputs.label                    - 
 * @param {Array} options.inputs.checked                  - 
 * @param {Array} options.inputs.id                       - id
 *
 * @return {Promise}
 * */
/**
 * @function dismiss
 * @description
 * ,  `Promise`  `resolve` 
 * @return {Promise}
 * */

import AlertComponent from './alert.vue'
import GeneratePopUpInstance from '../../util/GeneratePopUpInstance.js'

class AlertInstance extends GeneratePopUpInstance {
  isPresentHandled (options) {
    return !options.isH5 &&
      window.VM &&
      window.VM.platform &&
      window.VM.platform.alert &&
      window.VM.platform.alert(options)
  }
}

export default new AlertInstance(AlertComponent, 'alertPortal')



