<template>
    <article
            :version="version"
            :id="mode"
            :style="styleObj"
            :class="[mode,modeClass,platformClass,{'disable-hover':disableHover},{'disable-scroll':disableScroll}]"
            class="ion-app"
    >
        <section class="app-root">
            <slot></slot>
        </section>
        <aside id="modalPortal"></aside>
        <aside id="sheetPortal"></aside>
        <aside id="alertPortal"></aside>
        <aside id="loadingPortal"></aside>
        <aside id="toastPortal"></aside>
        <aside
                :class="[{'click-block-enabled':isClickBlockEnabled,'click-block-active':isClickBlockActive}]"
                class="click-block"
        />
    </article>
</template>
<script type="text/javascript">
  import Vue from 'vue'
  import { isString } from '../../util/type'
  import disableHover from '../../util/disable-hover'
  import modeMixin from '../../mixins/mode-mixin/index.js'
  import { version } from '../../../package.json'

  const CLICK_BLOCK_BUFFER_IN_MILLIS = 64       // 
  const CLICK_BLOCK_DURATION_IN_MILLIS = 700    // 

  export default {
    name: 'App',
    mixins: [modeMixin],
    provide () {
      const _this = this
      return {
        appComponent: _this
      }
    },
    data () {
      return {
        disableHover: disableHover,     // 
        isScrollDisabled: false,        // 
        disableScroll: false,

        isClickBlockEnabled: false,     //  click-block-enabled
        isClickBlockActive: false,      //  '' click-block-active
        clickBlockTimer: null,
        disableScrollTimer: null,

        isScrolling: false,             // 
        isEnabled: true,                // 
        scrollTop: 0,                   // scrollTop, disable scroll
        styleObj: {},

        version: version
      }
    },
    computed: {
      platformClass () {
        return `platform-${this.mode}`
      }
    },
    methods: {
      /**
       * @function setEnabled
       * @description
       * , ActionSheet/Alert/Modaltransition,
       * transitionApp, , 
       * @param {boolean} isEnabled  - `true` for enabled, `false` for disabled
       * @param {number} duration - isEnabled=false  `isEnabled` `false`, duration`isEnabled``true`
       *
       * @example
       * this.$app && this.$app.setEnabled(false, 400) -> 400ms, 400ms
       * this.$app && this.$app.setEnabled(true) -> 64ms
       * */
      setEnabled (isEnabled, duration = CLICK_BLOCK_DURATION_IN_MILLIS) {
        if (isEnabled) {
          // 
          if (this.isClickBlockActive) {
            this.timer && window.clearTimeout(this.timer)
            this.timer = window.setTimeout(() => {
              this.isClickBlockActive = false
              this.isEnabled = true
            }, CLICK_BLOCK_BUFFER_IN_MILLIS)
          } else {
            this.isEnabled = true
          }
        } else {
          // 
          this.isEnabled = false
          this.isClickBlockActive = true
          this.timer && window.clearTimeout(this.timer)
          this.timer = window.setTimeout(() => {
            this.isClickBlockActive = false
            this.isEnabled = true
          }, duration + CLICK_BLOCK_BUFFER_IN_MILLIS)
        }
      },

      /**
       * @function setDisableScroll
       * @description
       * , 
       * @param {Boolean} isScrollDisabled -  true:/false:
       * @param {number} duration - 
       * @example
       * this.$app && this.$app.setDisableScroll(true) -> 
       * this.$app && this.$app.setDisableScroll(true, 400) -> 400ms, 400ms
       * this.$app && this.$app.setDisableScroll(false) ->
       * */
      setDisableScroll (isScrollDisabled, duration = 0) {
        if (isScrollDisabled) {
          // duration ms, duration ms
          this.$_disableScroll()
          if (duration > 0) {
            this.disableScrollTimer && window.clearTimeout(this.disableScrollTimer)
            this.disableScrollTimer = window.setTimeout(() => {
              this.$_enableScroll()
            }, duration)
          }
        } else {
          // 
          this.$_enableScroll()
        }
      },

      /**
       * @function setClass
       * @description
       * class, 
       * @param {string} className - 
       * @param {boolean} [isAdd=false] - 
       */
      setClass (className, isAdd = false) {
        if (isAdd) {
          this.$el.classList.add(className)
        } else {
          this.$el.classList.remove(className)
        }
      },

      /**
       * @function setDocTitle
       * @param {String|Object}  _title - 
       * @param {String}  _title.title - 
       * @description
       * document.title, string, title, , title
       * */
      setDocTitle (_title) {
        if (this.$platform && this.$platform.setNavbarTitle) {
          this.setDocTitle = (title) => {
            if (isString(title)) {
              title = {title: title}
            }
            this.$platform.setNavbarTitle(title)
          }
        } else {
          this.setDocTitle = (title) => {
            if (isString(title)) {
              title = {title: title}
            }
            window.setTimeout(() => {
              document.title = title.title || ''
            }, 16 * 5)
          }

        }
        this.setDocTitle(_title)
      },

      /**
       * @param {Boolean} shouldShow
       * @param {Number} [expire=100]
       * @return {Promise}
       * @private
       * */
      $_activate (shouldShow, expire = 100) {
        return new Promise((resolve) => {
          if (this.isEnabled !== shouldShow) {
            this.isEnabled = shouldShow

            window.setTimeout(() => {
              this.isEnabled = false
            }, expire)
          } else {
            resolve()
          }
        })
      },

      /**
       * @private
       * */
      $_disableScroll () {
        if (!this.isScrollDisabled) {
          this.$root.$emit('app:disableScroll')
          this.scrollTop = 0
          this.styleObj = {}
          this.isScrollDisabled = true

          // , ()
          let windowHeight = window.innerHeight
          let docHeight = window.document.documentElement.offsetHeight
          if (windowHeight >= docHeight) {
            this.disableScroll = false
            // `:${windowHeight}, : ${docHeight}, setDisableScroll.`
          } else {
            this.disableScroll = true
            this.scrollTop = window.scrollY
            this.styleObj = {
              top: -this.scrollTop + 'px'
            }
          }
        }
      },

      /**
       * @private
       * */
      $_enableScroll () {
        if (this.isScrollDisabled) {
          this.$root.$emit('app:enableScroll')
          this.isScrollDisabled = false
          this.disableScroll = false
          this.$nextTick(() => {
            // scrollTop lost after set position:fixed, restore it back.
            let windowHeight = window.innerHeight
            let docHeight = window.document.documentElement.offsetHeight
            if (windowHeight < docHeight && this.scrollTop > 0) {
              window.scrollTo(0, this.scrollTop)
            }
          })
        }
      }
    },
    created () {
      // install App Instance to prototype
      Vue.prototype.$app = this

      this.$root.$on('onScrollStart', () => {
        this.isScrolling = true
      })
      this.$root.$on('onScroll', () => {
        this.isScrolling = true
      })
      this.$root.$on('onScrollEnd', () => {
        this.isScrolling = false
      })

      this.$root.$emit('app:created', this)
    },
    mounted () {

      // 
      this.isClickBlockEnabled = true
      this.$root.$emit('app:mounted', this)
    }
  }
</script>



