<template>
    <div class="ion-infinite-scroll" :threshold="threshold" :state="state">
        <slot></slot>
    </div>
</template>
<script type="text/javascript">
  import { setElementClass } from '../../util/util'

  const STATE_ENABLED = 'enabled'
  const STATE_DISABLED = 'disabled'
  const STATE_LOADING = 'loading'
  export default {
    name: 'InfiniteScroll',
    inject: {
      contentComponent: {
        from: 'contentComponent',
        default: null
      }
    },
    props: {
      // 
      enabled: {
        type: Boolean,
        default: true
      },
      // 
      threshold: {
        type: String,
        default: '15%' // 15%  150px
      }
    },
    data () {
      return {
        lastCheck: 0,  // , 32ms
        state: (this.enabled ? STATE_ENABLED : STATE_DISABLED) // 
      }
    },
    watch: {
      enabled () {
        this.enable(this.enabled)
      }
    },
    computed: {
      // 
      thr () {
        return this.threshold
      },
      // (px)
      thrPx () {
        if (this.threshold.indexOf('%') > -1) {
          return 0
        } else {
          return parseFloat(this.threshold)
        }
      },
      // ()
      thrPc () {
        if (this.threshold.indexOf('%') > -1) {
          return (parseFloat(this.threshold) / 100)
        } else {
          return 0
        }
      }
    },
    methods: {
      /**
       * @function complete
       * @description
       *  `onInfinite` (InfiniteScrollthis)`complete()`, .
       * AJAX, UI, `complete()`,
       * loading, InfiniteScroll`loading`  `enabled`.
       * */
      complete () {
        this.$nextTick(() => {
          if (this.state === STATE_LOADING) {
            this.state = STATE_ENABLED
            // , 
            this.contentComponent.resize()
          }
        })
      },

      /**
       * @function enable
       * @description
       * InfiniteScroll. InfiniteScroll.
       * `onInfinite`  `infiniteScroll.enable(false)`.
       * @param {boolean} shouldEnable - , `false`, scroll, 
       */
      enable (shouldEnable) {
        this.state = (shouldEnable ? STATE_ENABLED : STATE_DISABLED)
        this.$_setListeners(shouldEnable)
      },

      /**
       * @function waitFor
       * @description
       * Promise, Promise, `resolve()`, `resolve()`
       * `this.complete()`, 
       * @param {Promise} action - 
       *
       * @example
       *
       * <Content>
       *  <List>
       *      <Item v-for="item of items">{{item}}</Item>
       *  </List>
       *  <InfiniteScroll @onInfinite="$event.waitFor(doInfinite())">
       *      <InfiniteScrollContent></InfiniteScrollContent>
       *  </InfiniteScroll>
       * </Content>
       *
       * @example
       * // ...
       *   doInfinite () {
       *     console.log('Begin async operation');
       *     return new Promise((resolve) => {
       *       setTimeout(() => {
       *         for (var i = 0; i < 30; i++) {
       *           this.items.push( this.items.length );
       *         }
       *         console.log('Async operation has ended');
       *         resolve();
       *       }, 500);
       *     })
       * }
       * // ...
       */
      waitFor (action) {
        const enable = this.complete.bind(this)
        action.then(enable, enable)
      },

      /**
       * @param {boolean} shouldListen - 
       * @private
       */
      $_setListeners (shouldListen) {
        if (shouldListen) {
          // ContentonScroll
          // NOTICE: Content`onScroll`
          this.contentComponent.$on('onScroll', this.$_onScrollHandler)
        } else {
          // onScroll(Content)
          this.contentComponent.$off('onScroll', this.$_onScrollHandler)
        }
      },

      /**
       * @param {ScrollEvent} ev - 
       * @return {Number} - 1:loading/disabled; 2:; 3:; 5:loading; 6:
       * @private
       * */
      $_onScrollHandler (ev) {
        if (this.state === STATE_LOADING || this.state === STATE_DISABLED) {
          return 1
        }

        if (this.lastCheck + 32 > ev.timeStamp) {
          // 32ms
          return 2
        }
        this.lastCheck = ev.timeStamp

        const infiniteHeight = this.$el.scrollHeight

        if (!infiniteHeight) {
          // 
          return 3
        }

        const d = this.contentComponent.scrollView.ev

        let reloadY = window.innerHeight

        if (this.thrPc) {
          reloadY += (reloadY * this.thrPc)
        } else {
          reloadY += this.thrPx
        }

        const distanceFromInfinite = ((d.scrollHeight - infiniteHeight) - d.scrollTop) - reloadY

        if (distanceFromInfinite < 0) {
          if (this.state !== STATE_LOADING && this.state !== STATE_DISABLED) {
            this.state = STATE_LOADING

            /**
             * @event component:InfiniteScroll#onInfinite
             * @description 
             * @param {InfiniteScroll} infiniteScroll - InfiniteScroll
             * */
            this.$emit('onInfinite', this)
          }
          return 5
        }
        return 6
      }
    },
    mounted () {
      if (!this.contentComponent) {
        console.warn(this.contentComponent, 'InfiniteScroll component must be used under Content component!')
        return
      }
      setElementClass(this.contentComponent.$el, 'has-infinite-scroll', true)
      this.$_setListeners(this.state !== STATE_DISABLED)
    },
    destroyed () {
      this.$_setListeners(false)
    }
  }
</script>



