/**
 * 
 * */
export default {
  // ()
  integer: {
    type: 'number',
    regexp: /^-?[1-9]\d*$/
  },
  // 
  positiveInteger: {
    type: 'number',
    regexp: /^[1-9]\d*$/
  },
  // 
  negativeInteger: {
    type: 'number',
    regexp: /^-[1-9]\d*$/
  },
  // 
  email: {
    type: 'email',
    regexp: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/
  },
  // IP
  ip: {
    type: 'number',
    regexp: /(25[0-5]|2[0-4]\d|[0-1]\d{2}|[1-9]?\d)\.(25[0-5]|2[0-4]\d|[0-1]\d{2}|[1-9]?\d)\.(25[0-5]|2[0-4]\d|[0-1]\d{2}|[1-9]?\d)\.(25[0-5]|2[0-4]\d|[0-1]\d{2}|[1-9]?\d)/
  },
  //  text
  idCard: {
    type: 'text',
    regexp (num) {
      if (!num) return false
      num = num.toUpperCase()
      const cityCode = {
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        21: '',
        22: '',
        23: ' ',
        31: '',
        32: '',
        33: '',
        34: '',
        35: '',
        36: '',
        37: '',
        41: '',
        42: ' ',
        43: '',
        44: '',
        45: '',
        46: '',
        50: '',
        51: '',
        52: '',
        53: '',
        54: ' ',
        61: '',
        62: '',
        63: '',
        64: '',
        65: '',
        71: '',
        81: '',
        82: '',
        91: ' '
      }

      if (!cityCode[num.substr(0, 2)]) {
        if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
          console.log('')
        }
        return false
      }
      // 1518151817X
      if (!/(^\d{15}$)|(^\d{17}([0-9]|X)$)/.test(num)) {
        if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
          console.log('\n1518X')
        }
        return false
      }
      // ISO 7064:1983.MOD 11-2X10
      // 
      var re, len, arrSplit, dtmBirth, bGoodDay, arrInt, arrCh, nTemp, k
      len = num.length
      /* istanbul ignore if */
      if (len === 15) {
        re = new RegExp(/^(\d{6})(\d{2})(\d{2})(\d{2})(\d{3})$/)
        arrSplit = num.match(re)

        // 
        dtmBirth = new Date(
          '19' + arrSplit[2] + '/' + arrSplit[3] + '/' + arrSplit[4]
        )
        bGoodDay =
          dtmBirth.getYear() === Number(arrSplit[2]) &&
          dtmBirth.getMonth() + 1 === Number(arrSplit[3]) &&
          dtmBirth.getDate() === Number(arrSplit[4])
        if (!bGoodDay) {
          if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
            console.log('')
          }
          return false
        } else {
          // 1518
          // ISO 7064:1983.MOD 11-2X10
          arrInt = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
          arrCh = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2']
          nTemp = 0
          num = num.substr(0, 6) + '19' + num.substr(6, num.length - 6)
          for (k = 0; k < 17; k++) {
            nTemp += num.substr(k, 1) * arrInt[k]
          }
          num += arrCh[nTemp % 11]
          return true
        }
      }
      if (len === 18) {
        re = new RegExp(/^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/)
        arrSplit = num.match(re)

        // 
        dtmBirth = new Date(arrSplit[2] + '/' + arrSplit[3] + '/' + arrSplit[4])
        bGoodDay =
          dtmBirth.getFullYear() === Number(arrSplit[2]) &&
          dtmBirth.getMonth() + 1 === Number(arrSplit[3]) &&
          dtmBirth.getDate() === Number(arrSplit[4])
        if (!bGoodDay) {
          if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
            console.log('')
          }
          return false
        } else {
          // 18
          // ISO 7064:1983.MOD 11-2X10
          var valnum
          arrInt = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
          arrCh = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2']
          nTemp = 0
          for (k = 0; k < 17; k++) {
            nTemp += num.substr(k, 1) * arrInt[k]
          }
          valnum = arrCh[nTemp % 11]
          if (valnum !== num.substr(17, 1)) {
            if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
              console.log('18' + num.substr(17, 1) + '->' + valnum)
            }
            return false
          }
          return true
        }
      }
      /* istanbul ignore next */
      return false
    }
  },
  // 6-18
  password: {
    type: 'password',
    regexp: /^[a-zA-Z]\w{5,17}$/
  },
  // , XXXX-XXXXXXXXXXX- XXXXXXXXXXX-XXXXXXXXXX-XXXXXXXXXXXXXXXXXXXXXXX
  tel: {
    type: 'tel',
    regexp: /^(\(\d{3,4}\)|\d{3,4}-)?\d{7,8}$/
  },
  // , 13/14/15/18/17
  mobile: {
    type: 'tel',
    regexp: /^((13[0-9])|(14[0-9])|(15[0-9])|(18[0-9])|(17[0-9]))\d{8}$/
  },
  // 
  cn: {
    type: 'text',
    regexp: /[\u4e00-\u9fa5]/
  },
  // , 4
  securityCode: {
    type: 'number',
    regexp: /^\d{4,}$/
  },
  // : "-""_"
  nickName: {
    type: 'text',
    regexp: /[A-Za-z0-9_\-\u4e00-\u9fa5]+/
  },
  // qq: 1-95
  qq: {
    type: 'number',
    regexp: /^[1-9][0-9]{4,}$/
  },
  // URL, (https|http|ftp|rtsp|mms)
  url: {
    type: 'url',
    regexp: /^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+/
  }
}



