<template>
    <label class="ion-label label" :class="[modeClass,colorClass]"
           :fixed="fixed"
           :floating="floating"
           :stacked="stacked">
        <slot></slot>
    </label>
</template>
<script type="text/javascript">
  /**
   * @component Label
   * @description
   *
   * ##  / Label
   *
   * ### 
   *
   * LabelItem, Input/Toggle/CheckboxFrom. LabelLabel: inline()/fixed/floating/stacked.
   *
   * ### 
   *
   * input/Itemcontent.
   *
   * @props {String} mode - 
   * @props {String} color - 
   * @props {String} fixed - input
   * @props {String} floating - input, input
   * @props {String} stacked - input
   *
   * @slot  - 
   * @see component:Item
   * @see component:Input
   * @demo #/input-normal
   * */

  export default {
    name: 'Label',
    inject: {
      itemComponent: {
        from: 'itemComponent',
        default: null
      }
    },
    props: {
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      },
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: String,

      // label
      fixed: Boolean,
      floating: Boolean,
      stacked: Boolean
    },
    computed: {
      // 
      modeClass () {
        return `label-${this.mode}`
      },
      // 
      colorClass () {
        return this.color ? (`label-${this.mode}-${this.color}`) : ''
      }
    },
    mounted () {
      /**
       * fixed/floating/stackedinputlabel
       * fixed: label, input
       * floating:  input, labelinput; input, 
       * stacked: floating, , 
       * */
      if (this.itemComponent) {
        this.itemComponent.labelClass['item-label-fixed'] = this.fixed
        this.itemComponent.labelClass['item-label-floating'] = this.floating
        this.itemComponent.labelClass['item-label-stacked'] = this.stacked
      }
    }
  }

</script>



