import PickerComponent from './picker.vue'
import GeneratePopUpInstance from '../../util/GeneratePopUpInstance.js'

/**
 * @function present
 * @param {object} options - 
 * @param {Object} options.buttons - button
 * @param {Array} options.columns - column
 * @param {String} options.column.name - column
 * @param {String} options.columns.align - column
 * @param {String} [options.mode='ios'] - 
 * @param {String} [options.cssClass] - 
 * @param {Boolean} [options.enableBackdropDismiss=true] - backdrop
 * @param {Function} [options.onChange=noop] - picker, col, 
 * @param {Function} [options.onSelect=noop] - 
 * @description
 * 
 * @private
 * */
/**
 * @function dismiss
 * @description
 * 
 * @private
 * */
class PickerInstance extends GeneratePopUpInstance {
  isPresentHandled (options) {
    return !options.isH5 &&
      window.VM &&
      window.VM.platform &&
      window.VM.platform.picker &&
      window.VM.platform.picker(options)
  }

  /**
   * 
   * @private
   * */
  refresh () {
    this._ins && this._ins.refresh()
  }

  /**
   * 
   * @private
   * */
  resetColumn (index) {
    this._ins && this._ins.resetColumn(index)
  }
}

export default new PickerInstance(PickerComponent, 'sheetPortal')

/**
 * @component Picker
 * @description
 *
 * ##  / Picker
 *
 * ### 
 *
 * Picker, Select, Select, . , , Picker, Demo.
 *
 * ### 
 *
 * Picker, , Datetime/CitySelect. Picker. :
 *
 *
 * #### 1. buttons
 *
  / Name |  / Type |  / Description
 ----------|-----------|--------------------------------
 text      | string    | 
 role      | string    | , rolecancel, 
 handler   | function  | 
 *
 * #### 2. columns
 *
  / Name     |  / Type          |  / Description
 --------------|--------------------|---------------------------
 name          | string             | column
 align         | string             | column
 selectedIndex | number             | column(index)
 prefix        | string             | 
 prefixWidth   | string             | , '50px'
 suffix        | string             | 
 suffixWidth   | string             | , '50px'
 cssClass      | string             | column, 
 columnWidth   | string             | column, 
 optionsWidth  | string             | column
 options       | PickerColumnOption | column
 *
 *
 * #### 3. columnsoptions
 *
  / Name |  / Type |  / Description
 ----------|-----------|------------------
 text      | string    | 
 value     | *         | 
 disabled  | boolean   | 
 *
 *
 * #### 4. onChange
 *
 * , .
 *
 * #### 5. onSelect
 *
 * , 
 *
 * ### alipay
 *
 * H5options`isH5=true`, alipay.
 *
 * ### 
 *
 * , , `Picker.present(...)`
 *
 * ```
 *  import { Picker } from 'vimo'
 * ```
 *
 * @demo #/picker
 * */



