/**
 * popup
 * */
import focusOutActiveElement from './focus-out-active-element'

const NOOP = () => {}

export default {
  data () {
    return {
      /**
       * Animate Data
       * */
      enabled: true,         // 
      disableDuring: 200,     // 
      isActive: false,        // 
      dismissCallback: NOOP,  // dismiss
      presentCallback: NOOP  // present
    }
  },
  methods: {
    /**
     * Transition Animate
     * Animate Hooks
     * */
    beforeEnter () {
      this.$emit('animate:beforeEnter')
      this.$app && this.$app.setEnabled(false, this.disableDuring)
      this.enabled = false // 
      focusOutActiveElement()
    },
    afterEnter () {
      this.$emit('animate:afterEnter')
      this.presentCallback()
      this.enabled = true
    },
    beforeLeave () {
      this.$emit('animate:beforeLeave')
      this.$app && this.$app.setEnabled(false, this.disableDuring)
      this.enabled = false
      focusOutActiveElement()
    },
    afterLeave () {
      this.$emit('animate:afterLeave')
      this.dismissCallback()
      this.enabled = true
    },

    /**
     * @function present
     * @description
     * 
     * @returns {Promise} resolved
     * @private
     */
    present () {
      this.isActive = true
      this.$emit('animate:present')
      return new Promise((resolve) => { this.presentCallback = resolve })
    },

    /**
     * @function dismiss
     * @description
     * 
     * @returns {Promise} resolved
     * @private
     */
    dismiss (data) {
      if (this.isActive) {
        this.isActive = false
        this.$emit('animate:dismiss', data)
        return new Promise((resolve) => { this.dismissCallback = resolve })
      } else {
        return new Promise((resolve) => { resolve() })
      }
    }
  }
}



